import {Component, Input} from '@angular/core';
import {PostalService} from "../../app/_services/postal.service";
import {DomSanitizer, SafeResourceUrl} from "@angular/platform-browser";

@Component({
  selector: 'webpage',
  template: `
    <iframe [src]="sanitizedSourceUrl" frameborder="0" width="100%" height="740px"></iframe>
  `
})
export class WebPage {
  sanitizedSourceUrl: SafeResourceUrl;

  @Input('sourceUrl')
  set sourceUrl(newSourceUrl: string) {
    this.sanitizedSourceUrl = this.sanitizer.bypassSecurityTrustResourceUrl(newSourceUrl);
  }

  constructor(private postalService: PostalService,
              private sanitizer: DomSanitizer) {
  }
}



